@import "fonts.scss";
@import "colors.scss";
@import "animation.scss";

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background-color: $bg-color;
  color: $main-color;
  font-family: 'Segoe UI';
  font-size: 16px;
}
.container{
  width: 90%;
  max-width: 1440px;
  margin: 0 auto;
}

h1{
  font-family: 'Odibee Sans';
  font-size: 24px;
  margin: 1.5em 0 1em;
  color: $second-color;
}
a{
  text-decoration: none;
}
ul{
  list-style: none;
}

//buble

.box {
  position: relative;
  width: 450px;
  height: 450px;
  z-index: 10;
}

.load-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: $bg-color;
  
}
canvas{
  position: absolute;
}




//burger menu

header{
  display: flex;
  justify-content: space-between;
  padding: 2rem 0;
  align-items: center;
}
.section__header{
  height: 15vh;
}
.logo{
  display: flex;
  align-items: center;
}
.logo img{
  width: 75px;
  margin-right: 10px;
}
.header__logo{
  color: $main-color;
  font-family: 'Holtwood One SC';
  font-size: 32px;
  margin: 0;
  span{
    font-family: 'Mr Dafoe';
    font-weight: 400;
  }
  
  
}
.nav__list{
  display: flex;
  align-items: center;
}
.nav__item{
  margin: 0 8px;
}
.nav__link{
  padding: 10px;
  color: $main-color;
  font-weight: bold;
  border-radius: 5px;
  position: relative;
}
.nav__link::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: $second-color;
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

.nav__link:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}
.header__toggle,.header__close{
  display: none;
}
.active::before{
  transform-origin: left;
  transform: scaleX(1);
}

