}
.single-services {
  background-color: $bg-color;
  -webkit-box-shadow: 0px 5px 30px 0px rgba(241, 208, 53, 0.16);
  box-shadow: 0px 5px 30px 0px rgba(241, 208, 53, 0.16);
  -webkit-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
  padding: 50px 30px;
  border: 2px solid transparent;
  border-radius: 8px;
  max-width: 66.66666667%;
  margin: 25px;
  width: 33.33333333%;
}
// @media (min-width: 992px){
