    color: $main-color;
    font-size: 24px;
    cursor: pointer;
  }
  .header__close{
    position: absolute;
    right: 24px;
    display: block;
    font-size: 24px;
    border-radius: 50%;
    cursor: pointer;
  }
  .nav{
    position: fixed;
    top: 0;
    right: -100%;
    background-color: $bg-color;
    color: $main-color;
    width: 100%;
    height: 100vh;
    padding: 24px 0;
    z-index: 100;
    transition: 0.5s;
  }
  .nav__list{
    display: flex;
    flex-direction: column;
  }
  .nav__item{
    margin: 2rem 0;
  }
  .show{
    right: 0;
  }
  //footer
  footer .container{
    flex-direction: column;
  }
}

